@import url(https://fonts.googleapis.com/earlyaccess/cwtexkai.css);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexyen.css);
@import url(https://fonts.googleapis.com/earlyaccess/cwtexfangsong.css);
@import url(https://fonts.googleapis.com/earlyaccess/notosanstc.css);
@import url(https://fonts.googleapis.com/css2?family=Playwrite+NZ:wght@100..400&display=swap);
/* 楷體 */ /* 圓體 */ /* 仿宋體 */ /* 黑體 */ /* Playwrite New Zealand */

body {
  font-family: Arial, sans-serif;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  background-color: #e8e1d4; /* 背景色 */
}

.App {
  padding: 20px;
  max-width: 100%;
  margin: auto;
}

.header {
  display: flex;
  justify-content: center;
}

.body {
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: space-between; */
}

.mainBody {
  display: flex;
  flex-direction: column;
  width: 100%;
  /* max-width: 1000px; */
}

.ad {
  display: flex;
  align-items: center;
  flex-direction: column;
  width: 100px;
  /* display: none; Hide ad on mobile */
}

.tabs {
  display: flex;
  flex-wrap: wrap;
  /* flex-grow: 1; */
  justify-content: space-around;
  margin-bottom: 10px;
  padding: 10px;
  border-bottom: 3px solid #f7b87e; /* 次色調邊框 */
  border-radius: 10px 10px 0 0; /* 圓角 */
}

.tablinks {
  background-color: #f7b87e; /* 次色調背景 */
  border: none;
  cursor: pointer;
  padding: 10px 20px;
  margin-right: 5px;
  flex: 1 0 40%;
  /* flex: 1; */
  text-align: center;
  color: #302a25; /* 文字顏色 */
  font-weight: bold;
  border-radius: 10px; /* 圓角 */
  transition: background-color 0.3s ease;
}

.tablinks:hover {
  background-color: #f09f56; /* 主色調 */
}

.tablinks.active {
  background-color: #f09f56; /* 活動時背景色 */
  color: #302a25; /* 活動時文字顏色 */
}

.tabcontent,
.activity {
  display: block; /* 預設顯示內容 */
  padding: 20px;
  border-top: none;
  background-color: #efceae; /* 白色背景 */
  border-radius: 0 0 10px 10px; /* 圓角 */
  color: #302a25; /* 文字顏色 */
}

.bulletin-board {
  display: block; /* 預設顯示內容 */
  padding: 10px;
  /* border-top: none; */
  background-color: #efceae; /* 背景 */
  border-radius: 10px 10px 0px 0px; /* 圓角 */
  color: #423d38; /* 文字顏色 */
  margin-bottom: 5px;
  font-size: 1rem;
  width: 100%;
  box-sizing: border-box;
  /* font-size: 2vmin */
}

.input-row {
  margin-top: 10px;
  margin-bottom: 10px;
  display: flex;
  flex-wrap: wrap;
  align-items: center; /* Align items to the center */
}

/* .input-row :hover { */
  /* color: #e69b4c; 滑鼠懸停在連結時的顏色變化 */
  /* border-color: #e69b4c; */
/* } */

.input-container {
  padding: 0px 10px;
  display: flex;
  flex-direction: column; /* Ensure error message is below input */
  flex: 1 0 100%;
  /* flex: 1; */
}

.input-row input {
  /* margin-top: 10px; */
  margin-right: 10px;
  padding: 5px;
  width: calc(100% - 150px);
  height: 20px;
  border: 2px solid #f7b87e; /* 次色調邊框 */
  border-radius: 4px;
  transition: border-color 0.3s ease;
}

.input-row .clicked {
  color: grey;
  text-decoration: line-through;
}

.paste-icon,
.save-icon {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 30px;
  height: 30px;
  font-size: 20px;
  padding: 3px;
  margin: 5px;
  color: #f7b87e;
  cursor: pointer;
  transition: color 0.3s ease, border-color 0.3s ease;
  border: 2px solid #f7b87e;
  border-radius: 20%;
}

.paste-icon:hover,
.save-icon:hover {
  color: #e69b4c; /* 滑鼠懸停時的顏色變化 */
  border-color: #e69b4c;
}

.error-message {
  color: #ec5353; /* 錯誤訊息顏色 */
  font-size: 0.9em;
  margin-top: 5px;
}

.subtabs {
  display: flex;
  flex-wrap: wrap;
  margin: 10px;
}

.subtablinks {
  background-color: #f7b87e; /* 次色調背景 */
  border: none;
  cursor: pointer;
  padding: 10px;
  margin-right: 5px;
  margin-bottom: 8px;
  flex: 1 0 40%;
  /* flex: 1; */
  text-align: center;
  color: #302a25; /* 文字顏色 */
  font-weight: bold;
  border-radius: 10px; /* 圓角 */
  transition: background-color 0.3s ease;
}

.subtablinks:hover {
  background-color: #f09f56; /* 主色調 */
}

.subtablinks.active {
  background-color: #f09f56; /* 活動時背景色 */
  color: #302a25; /* 活動時文字顏色 */
}

.footer {
  display: flex;
  flex-direction: column;
  margin-top: 50px;
  background-color: #e8e1d4;
  border-top: 1.5px solid #DBD1BD;
  max-width: 100%;
}

.footer-main {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-between;
  padding:30px 20px;
}

.footer-left,
.footer-right {
  width: 100%;
  text-align: center;
  margin-bottom: 20px;
}

.footer-left {
  font-family: 'Noto Sans TC', sans-serif;
  font-size: 0.9rem;
  color: #635e55;
}

.footer-logo {
  display: flex;
  justify-content: center;
  font-family: "Playwrite NZ", cursive;
  font-optical-sizing: auto;
  color: #f09f56;
  font-weight: bold;
}

.brand-logo {
  width: 50px;
  margin-bottom: 10px;
  margin-right: 10px;
}

.footer-email-text {
  font-family: 'Noto Sans TC', sans-serif;
  color: #4b4842;
}

.footer-right span {
  font-size: 1.2em;
  font-family: 'Noto Sans TC', sans-serif;
  text-shadow: 1px 2px 2px gray;
}

.copyright {
  display: flex;
  justify-content: center;
  color: #635e55;
  font-size: 12px;
  margin-top: 20px;
}

@media (min-width: 768px) {
  .body {
    flex-direction: row;
    justify-content: space-between;
  }

  .mainBody {
    width: calc(100% - 120px);
    margin: 20px 30px;
  }

  .ad {
    display: flex;
    align-items: center;
    flex-direction: column;
    width: 100px;
  }

  .tablinks,
  .subtablinks {
    flex: 1 1;
  }

  .footer-main {
    flex-direction: row;
    justify-content: space-between;
    align-items: center;
    margin: 0px 100px;
    padding: 30px 20px;
  }

  .footer-left,
  .footer-right {
    width: auto;
    text-align: left;
    margin-bottom: 0;
  }

  .footer-left {
    font-size: 1.65vmin;
  }

  .footer-right {
    align-items: flex-start;
  }

  .copyright {
    font-size: 1.7vmin;
  }
}

